import { Component, OnInit } from '@angular/core';
import { NgModule } from '@angular/core';

import { PriceComponent } from '../price/price.component';
import { StrategyComponent } from '../strategy/strategy.component';
import { ActionComponent } from '../action/action.component';
import { StatusComponent } from '../status/status.component';

import { CryptoCompareSocketService } from '../crypto-compare-socket.service'

@Component({
  selector: 'app-currency',
  templateUrl: './currency.component.html',
  styleUrls: ['./currency.component.css'],
  providers: [CryptoCompareSocketService]
})

export class CurrencyComponent implements OnInit {
  currencyLiveInfoParent = [
    ['BTC~USD',{}],
    ['ETH~USD',{}],
    ['LTC~USD',{}],
    ['BCH~USD',{}],
    ['DASH~USD',{}]
  ];

  constructor(private cryptoCompareSocketService: CryptoCompareSocketService) {}

  ngOnInit() {

    this.cryptoCompareSocketService.getLiveData()
      .subscribe(
        liveData=> { 
          let found = this.currencyLiveInfoParent.find((element)=>{
            return element[0]===liveData['currencyPair']
          })
          found[1]= liveData
        }  
      )
  }

}



