import { Component, OnInit } from '@angular/core';
import { callExcService } from '../call-exchange.service'

@Component({
  selector: 'app-action',
  templateUrl: './action.component.html',
  styleUrls: ['./action.component.css']
})
export class ActionComponent implements OnInit {

  constructor(private callExchangeService:callExcService) { }

  buyOrderInfoMsg = ""
  sellOrderInfoMsg = ""

  ngOnInit() {

  }

  onSubmit(value:any, event){
    console.log(value)
    console.log(event)
    this.callExchangeService.sendExchangeTrade(value, event)
     .subscribe(
      (response:any) => {
        console.log(response)
        if(response.json().success === true && event === 'buy'){
          this.buyOrderInfoMsg = "Buy Order Successful - Transaction #" + response.result.uuid
          }
        else if(response.json().success === false && event === 'buy'){
          this.buyOrderInfoMsg = "Buy Order Unsuccessful"  
        }    
        else if(response.json().message && event ==='buy'){
          this.buyOrderInfoMsg = response.json().message
        }
     
        if(response.json().success === true && event === 'sell'){
          this.sellOrderInfoMsg = "Sell Order Successful - Transaction #" + response.result.uuid
          }
        else if(response.json().success === false && event === 'sell'){
          this.sellOrderInfoMsg = "Sell Order Unsuccessful"  
        }  
        else if(response.json().message && event ==='sell'){
          this.sellOrderInfoMsg = response.json().message
        }
      },
      (error) => console.log(error)
    )
  }



}



