import { Component, OnInit } from '@angular/core';
import { callExcService } from '../call-exchange.service'

@Component({
  selector: 'app-status',
  templateUrl: './status.component.html',
  styleUrls: ['./status.component.css']
})
export class StatusComponent implements OnInit {

  walletInfo:any=[]

  constructor(private callExchangeService:callExcService) { }

  ngOnInit() {
    this.callExchangeService.callExchangeInfo()
    .subscribe(
      walletData =>{
        console.log(walletData.json())
        this.walletInfo = walletData.json()
      }
    )
  }

}



